.contact-container {
  position: relative;
  height: 100vh;
  width: 100%;
  display: flex;
}
.box-container {
  height: 90vh;
  width: 90vw;
  display: flex;
}

.text-container {
  display: flex;
  flex-direction: column;
}
.text-container p:nth-of-type(1) {
  font-weight: 400;
  font-size: 0.75rem;
  //   margin: 1rem 2.5rem;
}

.text-container p:nth-of-type(2) {
  font-size: 0.6rem;
  margin-bottom: 3rem;
}
